html {
  height: 100%;
}
body {
  /* transition: background-color 1s; */
  background: url(./img/scene.png) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;  
}
@media (max-width: 400px) {
  body {
    background: url(./img/scene_400.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}
@media (max-width: 800px) {
  body {
    background: url(./img/scene_700.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}
@media (min-width: 1440px) {
  body {
    background: url(./img/scene_1440.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; 
  }
}

/* Main grid rules */
.mainGrid {
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;  
}
.footer {
  grid-row-start: 2;
  grid-row-end: 3;
  justify-self: center;
}
/* / Main grid rules */

/* Sub grid rules */
.container {
  min-height: 100%;
  display: grid;
  grid-template-rows: auto 2fr;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "header header header" 
    ". main . ";
}
.header {
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-areas:
    "logo nav nav"; 
}
.logo {
  grid-area: logo;
  grid-column-start: 1;
  grid-column-end: 2;
  padding-left: 1.5em;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.nav {
  grid-area: nav;
  grid-column-start: 2;
  grid-column-end: 4;
  margin-top: 1.5em;
  margin-bottom: 1.5em;
}
.main {
  grid-area: main;
  grid-column-start: 2;
  grid-column-end: 3;
}
@media (max-width: 700px) {
  .container {
    min-height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto 1fr;  
    grid-template-areas: 
      "header"
      "main";
  }
  .header {
    grid-column-start: 1;
    grid-column-end: 4;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas:
      "logo"
      "nav"; 
  }
  .logo {
    grid-area: logo;
    grid-row-start: 1;
    grid-row-end: 2; 
    justify-self: center;
    padding-left: 0;
    text-align: center;
    /* margin-top: 0.75em; */
    margin-bottom: 0em;
  }
  .nav {
    grid-area: nav;
    grid-row-start: 2;
    grid-row-end: 3; 
    justify-self: center;
    margin-top: 0em;
    margin-bottom: 0em;
  }
  .main {
    grid-area: main;
    grid-row-start: 2;
    grid-row-end: 3;
    margin-bottom: 1.5em;
  }
}
/* / Sub grid rules */

body, h1, ul, li, #root {
  margin: 0;
  padding: 0;
}
.typo {
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 1.75em;
  font-weight: 700;
  line-height: 1.5em;
  color: rgba(255,255,255,1);
}
@media (max-width: 700px) {
  .typo {
    font-size: 1.2em;
    font-weight: 700;
    line-height: 1.5em;
  }
}

.storybox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.fadeIn {
  /* margin-top: 3.85em; */
  text-align: center;
  opacity: 0;
  display: block;
  animation: slidein 1s ease-out forwards;
}
@keyframes slidein {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@media (max-width: 700px) {
  /* .fadeIn {
    margin-top: 0.5em;
  } */
  .fadeIn > p {
    margin-left: 1em;
    margin-right: 1em;
  }
  @keyframes slidein {
    0% {
      opacity: 0;
      transform: translateY(-35px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }
}
.button {
  margin-top: 1em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 2.5em;
  height: 3.5em;
  width: 11.5em;
  background-color: rgba(6,29,53,.75);
  color: rgba(255,255,255,1); 
}
.button:hover {
  -webkit-box-shadow: 0px 10px 54px -7px rgba(139,194,212,.75);
  -moz-box-shadow: 0px 10px 54px -7px rgba(139,194,212,.75);
  box-shadow: 0px 10px 54px -7px rgba(139,194,212,.75);
  background-color: rgba(6,29,53,.53);  
  color: rgba(157,220,242,1); 
  cursor: pointer;
}
button #share {
  position: absolute;
  left: 2em;
  bottom: 2em;
}
a {
  text-decoration: none;
  color: rgba(157,220,242,1);
}
a:hover {
  color: rgba(255,255,255,1); 
}
.button a {
  color: rgba(255,255,255,1);
}
.button a:hover {
  color: rgba(157,220,242,1); 
}
ul {
  list-style: none;
  /* padding-left: 2.5em; */
  margin-top: 0.25em; 
}
ul li {
  display: inline;
  padding-right: 1em; 
}
/* ul li a {
  text-decoration: underline;
} */
@media (max-width: 700px) {
  ul {
    padding-left: 0;
    margin-top: 0.75em;
    text-align: center;
  }
  ul li {
    display: inline;
    padding-left: 0.5em;
    padding-right: 0.5em;
  }
  ul li a {
    text-decoration: underline;
    color: rgba(157,220,242,1);
  }
}
.social {
  margin-top: 2.5em;
  margin-bottom: 2.5em;
}
@media (max-width: 700px) {
  .social {
    margin-top: 1.5em;
    margin-bottom: 1.5em;
  }
}
.unvisible {
  visibility: hidden;
}
.visible {
  visibility: visible;
}
.off {
  display: none;
}
.on {
  display: block;
}
svg.social-icon {
  width: 2em;
  height: 2em;
  margin-left: .125em;
  margin-right: .125em;
  fill: rgba(157,220,242,1);
}
svg.social-icon:hover {
  fill: rgba(255,255,255,1); 
}
